<template>
	<div class="create_one mt-5 position-relative">
		<b-container>
			<div class="header mt-4 d-flex align-items-center justify-content-between">
				<div class="h4 mb-0 font-weight-normal">{{ $t("createGroupInvite") }}</div>
				<div
					class="fs-sm text-muted mx-3 py-2 px-4 border border-transparent d-flex align-items-center justify-content-between"
					v-if="!invite.name"
					style="opacity: 0.6"
					v-b-tooltip.hover
					:title="$t('previewInviteTooltip')"
				>
					<BIcon icon="eye" scale="1.5" class="mr-2" />
					{{ $t("previewInvite") }}
				</div>
				<button class="fs-sm mx-3 filter_btn active" v-else @click="preview">
					<BIcon icon="eye" scale="1.5" class="mr-2" />
					{{ $t("previewInvite") }}
				</button>
			</div>
			<div class="field_title mt-4 pt-1">
				<div class="title">{{ $t("basicInfo") }}</div>
			</div>
			<b-form class="mt-4" @submit.prevent="next">
				<b-row>
					<!-- Invite name -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-input :label="$t('name')" :placeholder="$t('discoveryCall')" required v-model="invite.name" v-vuelidate="invite.name" v-visible="'name'">
							<template #icon_left>
								<BIcon icon="calendar2" font-scale="1.4" />
							</template>
						</form-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="name">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("inviteNameDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite maxInvitees -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-select :label="$t('maxInvitees')" v-model="invite.maxInvitees" :options="maxInviteesOptions" required v-visible="'maxInvitees'">
							<BIcon slot="icon_left" icon="people" />
						</form-select>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="maxInvitees">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("maxInviteesDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite location -->
					<b-col cols="12" lg="8" class="mb-4">
						<virtual-input :label="$t('location')" v-visible="'location'" @click="$bvModal.show('LocationModal')">
							<template #icon_left>
								<BIcon v-bind="location.iconProps" v-if="location.icon == 'BIcon'" />
								<component :is="location.icon" width="1.2em" v-bind="location.iconProps" v-else />
							</template>
							<BIcon slot="icon_right" icon="pencil" />
							<span slot="placeholder">
								<span v-if="!location.placeholder">{{ $t("locationDesc") }}</span>
								<span v-else class="text-dark">{{ location.placeholder }}</span>
							</span>
						</virtual-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="location">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("inviteLocationDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite description -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-text-area
							:label="$t('description')"
							:placeholder="$t('descriptionDesc')"
							v-model="invite.description"
							v-vuelidate="invite.description"
							v-visible="'description'"
						>
							<template #icon_left>
								<BIcon icon="text-paragraph" font-scale="1.4" scale="1.2" />
							</template>
						</form-text-area>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="description">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("inviteDescriptionDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite link -->
					<b-col cols="12" lg="8" class="mb-4">
						<virtual-input :label="$t('customUrl')" v-visible="'url'" @click="$bvModal.show('URLModal')">
							<BIcon slot="icon_left" icon="link" scale="1.2" />
							<BIcon slot="icon_right" icon="pencil" />
							<span slot="placeholder">
								<span v-if="!invite.url.length">{{ $t("setCustomUrl") }}</span>
								<span v-else>
									<span class="text-muted fs-md">https://maw3d.net/tersawy/</span>
									<span class="text-dark">{{ invite.url }}</span>
								</span>
							</span>
						</virtual-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="url">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("inviteCustomUrlDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite image -->
					<b-col cols="12" lg="8" class="mb-4">
						<virtual-input :label="$t('photoCover')" v-visible="'image'" :inputProps="{ class: 'cursor-auto' }">
							<BIcon slot="icon_left" icon="image" />
							<template #default>
								<div class="image_upload_wrapper">
									<div class="btns d-flex" :class="{ has_review: imageReview }">
										<span class="btn_icon btn_icon_md" v-b-tooltip.hover :title="$t('addPhoto')" v-b-modal.ImageModal>
											<CameraPlus width="1.7rem" />
										</span>
										<span v-if="imageReview" class="btn_icon btn_icon_md mx-4" v-b-tooltip.hover :title="$t('removePhoto')" @click="invite.image = null">
											<BIcon icon="trash" scale="1.5" />
										</span>
									</div>
									<img v-if="imageReview" :src="imageReview" alt="Cover" class="w-100 h-100" />
								</div>
							</template>
						</virtual-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="image">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<div class="ml-3">
								<p class="mb-0">
									{{ $t("invitePhotoCoverDesc") }}
								</p>
								<img src="../assets/media/cover_photo_one_tooltip.jpg" alt="Cover" style="margin-top: 8px; max-width: 100%" />
							</div>
						</div>
					</b-col>
				</b-row>
			</b-form>
		</b-container>
		<div class="footer_control bg-white border-top w-100">
			<b-container class="h-100">
				<div class="d-flex justify-content-end align-items-center h-100">
					<b-button variant="primary" @click="next" :disabled="$v.$invalid" class="px-3"> {{ $t("next") }} </b-button>
				</div>
			</b-container>
		</div>
		<LocationModal v-model="invite.location" />
		<URLModal v-model="invite.url" />
		<ImageModal v-model="invite.image" />
		<PreviewInvite ref="preview" />
	</div>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import FormSelect from "@/components/inputs/FormSelect";
	import FormTextArea from "@/components/inputs/FormTextArea";
	import VirtualInput from "@/components/inputs/VirtualInput";
	import CameraPlus from "@/components/icons/cameraPlus";
	import ZoomIcon from "@/components/icons/zoom";
	import GoogleMeetIcon from "@/components/icons/googleMeet";
	import LocationModal from "@/components/modals/LocationModal";
	import URLModal from "@/components/modals/URLModal";
	import ImageModal from "@/components/modals/ImageModal";
	import PreviewInvite from "@/components/PreviewInvite";

	import store from "@/store";

	import { validationMixin } from "vuelidate";

	import { required } from "vuelidate/lib/validators";

	export default {
		components: { FormInput, FormSelect, FormTextArea, VirtualInput, CameraPlus, ZoomIcon, GoogleMeetIcon, LocationModal, URLModal, ImageModal, PreviewInvite },

		mixins: [validationMixin],

		data() {
			return {
				invite: {
					name: "",
					location: { type: "", name: this.$t("none"), text: "" },
					description: "",
					url: "",
					image: "",
					maxInvitees: 2,
				},
			};
		},

		validations: {
			invite: {
				name: { required },
				location: {},
				description: {},
				url: {},
				image: {},
			},
		},

		async beforeRouteEnter(to, from, next) {
			store.commit("newInvite");

			let invite = store.state.invites.one;

			next((vm) => vm.init(invite));
		},

		computed: {
			maxInviteesOptions() {
				let options = [];

				for (let i = 2; i <= 100; i++) {
					options.push({ value: i, text: i });
				}

				return options;
			},

			location() {
				let location = this.invite.location;

				let type = location.type;

				let data = { placeholder: "", icon: "BIcon", iconProps: { icon: "geo-alt" } };

				if (type == "") return data;

				if (type == "zoom") return { placeholder: this.$t("zoom"), icon: ZoomIcon, iconProps: {} };

				if (type == "google") return { placeholder: this.$t("googleMeet"), icon: GoogleMeetIcon, iconProps: {} };

				if (type == "custom") return { placeholder: location.text, icon: "BIcon", iconProps: { icon: "geo-alt" } };
			},

			imageReview() {
				let image = this.invite.image;

				if (!image) return "";

				if (typeof image != "string") {
					// create url from file
					image = URL.createObjectURL(image);
				}

				return image;
			},
		},

		methods: {
			init(invite) {
				for (let key in invite) {
					this.invite[key] = invite[key];
				}
			},

			preview() {
				this.$store.commit("setInvite", this.invite);

				this.$nextTick(() => this.$refs.preview.open());
			},

			next() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$store.commit("setInvite", this.invite);

				this.$router.push("/create/group/when");
			},
		},
	};
</script>

