<template>
	<div class="dropdown_select_wrapper" :class="{ active: isFoucsed || isShown, required, icon_left: $slots.icon_left, is_invalid }">
		<div class="icon_left">
			<slot name="icon_left" v-bind="{ active: isFoucsed || isShown }"></slot>
		</div>
		<div class="dropdown_select_control">
			<span class="dropdown_select_label">{{ label }}</span>
			<b-dropdown
				v-if="!$slots.default"
				class="dropdown_select"
				block
				toggle-class="dropdown_select_btn d-flex justify-content-between align-items-center pl-0 "
				menu-class="w-100 p-0 shadow-lg border-0"
				variant="text"
				@shown="shown"
				@hidden="hidden"
			>
				<template #button-content>
					<slot name="label">
						<span v-if="selectedOption">{{ selectedOption.text }}</span>
					</slot>
				</template>
				<b-dropdown-item v-for="(item, index) in options" :key="index" link-class="p-3" @click="select(item)">
					{{ item.text }}
				</b-dropdown-item>
			</b-dropdown>
			<slot name="default"></slot>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			label: { type: String },
			value: { type: [String, Number] },
			required: { type: Boolean, default: false },
			options: { type: Array, default: () => [] },
			is_invalid: { type: Boolean, default: false },
		},

		data() {
			return {
				isFoucsed: false,
				isShown: false,
			};
		},

		mounted() {
			let dropdownBtn = document.querySelector(".dropdown_select_btn");

			if (dropdownBtn) {
				dropdownBtn.addEventListener("focus", () => {
					this.isFoucsed = true;
				});

				dropdownBtn.addEventListener("blur", () => {
					this.isFoucsed = false;
				});
			}
		},

		computed: {
			val: {
				get() {
					return this.value;
				},
				set(val) {
					this.$emit("input", val);
				},
			},

			selectedOption() {
				return this.options.find((option) => option.value == this.value);
			},
		},

		methods: {
			shown() {
				this.isShown = true;
			},

			hidden() {
				this.isShown = false;
			},

			select(item) {
				this.val = item.value;

				this.$emit("change", item.value);

				this.$emit("item-selected", item);
			},
		},
	};
</script>

<style lang="scss">
	.dropdown_select_wrapper {
		display: flex;
		width: 100%;
		&.icon_left {
			.icon_left {
				width: 36px;
				top: 25px;
				font-size: 19px;
				position: relative;
				align-self: flex-start;
				overflow: hidden;
				svg {
					width: 1em;
					height: 1em;
				}
			}
		}
		.dropdown_select_control {
			width: 100%;
			.dropdown_select_label {
				font-size: 12px;
				color: #828282;
				margin-bottom: 5px;
			}
			.dropdown_select {
				.dropdown_select_btn {
					border-bottom: 1px solid #ccc;
					border-bottom-left-radius: 0;
					border-bottom-right-radius: 0;

					&:before {
						content: "";
						position: absolute;
						bottom: 0;
						left: 50%;
						width: 0;
						height: 2px;
						background-color: #000;
						transition: 0.2s ease-out;
					}

					&:hover,
					&:focus,
					&[aria-expanded="true"] {
						&:before {
							left: 0;
							width: 100%;
						}
					}

					&:hover {
						border-bottom-color: transparent;
						background-color: transparent !important;
					}

					&:focus {
						outline: none;
						box-shadow: none;
						// background-color: #dae0e5 !important;
						background-color: #eceff3 !important;
					}

					&:focus,
					&[aria-expanded="true"] {
						&:before {
							background-color: var(--primary);
						}
					}
				}

				ul {
					max-height: 70vh;
					overflow-y: auto;
				}
			}
		}

		&.required {
			.dropdown_select_control {
				.dropdown_select_label {
					&:after {
						content: "*";
						margin-left: 4px;
					}
				}
			}
		}

		&.active {
			.icon_left {
				color: var(--primary);
			}
			.dropdown_select_control {
				.dropdown_select_label {
					color: var(--primary);
				}
			}
		}

		&.is_invalid {
			.icon_left {
				color: var(--danger);
			}
			.dropdown_select_control {
				.dropdown_select_label {
					color: var(--danger);
				}
				.dropdown_select {
					.dropdown_select_btn {
						&:before {
							left: 0;
							width: 100%;
							background-color: var(--danger) !important;
						}
					}
				}
			}
		}
	}
</style>
